.border-box-effect {
    position: relative;
    margin: 20px;
    display: inline-block;
    width: 43%;
}
.border-box-effect img {
    width: 100%;
    border-radius: 3px;
}
.border-box-effect:before {
    border-bottom: 3px solid #fff;
    border-left: 3px solid #fff;
    -webkit-transform-origin: 0 100%;
}
.border-box-effect:after {
    border-top: 3px solid #fff;
    border-right: 3px solid #fff;
    -webkit-transform-origin: 100% 0%;
}
.border-box-effect:after,
.border-box-effect:before {
    width: calc(100% - 22px);
    height: calc(100% - 22px);
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -o-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    z-index: 1;
}
.border-box-effect > :after,
.border-box-effect > :before {
    z-index: 2;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
}
.border-box-effect:hover:after,
.border-box-effect:hover:before {
    -webkit-transform: scale(1);
}
.border-box-effect > * {
    position: relative;
    display: block;
}
.border-box-effect > .new-anim:after {
    content: "\e92d";
    font-family: ingicdesign;
    color: #fb2224;
    font-size: 50px;
}
.border-box-effect > :before {
    background: rgba(0, 0, 0, 0.2);
    content: "";
    width: 100%;
    height: 100%;
    -webkit-transition: 0.6s;
    -moz-transition: 0.6s;
    -o-transition: 0.6s;
    transition: 0.6s;
}
.border-box-effect > :after {
    content: "+";
    font-size: 74px;
    color: #fff;
    text-align: center;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 40px;
    height: 40px;
    line-height: 36px;
    font-weight: 200;
    -webkit-transition: 0.6s;
    -moz-transition: 0.6s;
    -o-transition: 0.6s;
    transition: 0.6s;
}
