[class^=icon-] {
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
    opacity: .25
}

.slick-next:before,
.slick-prev:before {
    font-family: slick;
    font-size: 20px;
    color: #fff;
    opacity: 1
}

.slick-prev {
    left: -25px
}

[dir=rtl] .slick-prev {
    left: auto;
    right: -25px
}

.slick-prev:before,
[dir=rtl] .slick-next:before {
    content: "     "
}

.slick-next:before,
[dir=rtl] .slick-prev:before {
    content: "     "
}

.slick-next {
    right: -25px
}

[dir=rtl] .slick-next {
    left: -25px;
    right: auto
}

.slick-dots {
    z-index: 9;
    position: absolute;
    bottom: -30px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%
}

.intl-tel-input,
.slick-dots li {
    position: relative;
    display: inline-block
}

.\--big-dot .slick-dots {
    bottom: -40px
}

.\--xbig-dot .slick-dots {
    bottom: -60px
}

.slick-dots li {
    height: 10px !important;
    width: 10px !important;
    margin: 0 6px;
    cursor: pointer
}

.slick-dots li button {
    border: 0;
    display: block;
    height: 10px;
    width: 10px;
    line-height: 0;
    font-size: 0;
    padding: 5px;
    cursor: pointer
}

.slick-dots li button:before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: #48ada0;
    width: 16px;
    border-radius: 50%;
    height: 16px;
    font-family: slick;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    color: #333;
    opacity: .25;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slick-dots li.slick-active button:before {
    color: #fb2224;
    opacity: 1
}

.intl-tel-input * {
    -moz-box-sizing: border-box
}

.intl-tel-input input,
.intl-tel-input input[type=tel],
.intl-tel-input input[type=text] {
    position: relative;
    z-index: 0;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    padding-right: 36px;
    margin-right: 0
}

.intl-tel-input .flag-container {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    padding: 1px
}

.intl-tel-input .selected-flag {
    z-index: 1;
    position: relative;
    width: 36px;
    height: 100%;
    padding: 0 0 0 8px
}

.intl-tel-input .selected-flag .iti-flag {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto
}

.intl-tel-input .selected-flag .iti-arrow {
    position: absolute;
    top: 50%;
    margin-top: -2px;
    right: 6px;
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 4px solid #555
}

.intl-tel-input .selected-flag .iti-arrow.up {
    border-top: none;
    border-bottom: 4px solid #555
}

.intl-tel-input .country-list {
    position: absolute;
    z-index: 2;
    list-style: none;
    text-align: left;
    padding: 0;
    margin: 0 0 0 -1px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, .2);
    background-color: #fff;
    border: 1px solid #ccc;
    white-space: nowrap;
    max-height: 200px;
    overflow-y: scroll;
    width: 100%
}

.intl-tel-input .country-list.dropup {
    bottom: 100%;
    margin-bottom: -1px
}

.intl-tel-input .country-list .flag-box {
    display: inline-block;
    width: 20px
}

@media (max-width:500px) {
    .intl-tel-input .country-list {
        white-space: normal
    }
}

.intl-tel-input .country-list .divider {
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #ccc
}

.intl-tel-input .country-list .country {
    padding: 5px 10px;
    display: block;
    width: 100%;
    text-align: left
}

.intl-tel-input .country-list .country .dial-code {
    color: #999
}

.intl-tel-input .country-list .country-name,
.intl-tel-input .country-list .flag-box {
    margin-right: 6px
}

