#diclimericon i {
    position: fixed;
    bottom: 10px;
    left: 15px;
    padding: 6px;
    z-index: 999990;
    box-sizing: border-box;
    font-size: 3rem;
    background: #ff9900;
    color: #000;
    cursor: pointer;
    border-radius: 50%;
    box-shadow: 0px 0 39px -5px;
}
.cookie-consent-banner__inner {
    max-width: 70%;
    margin: 0 0px 0 20px;
    padding: 6px 0;
}

.cookie-consent-banner__btn {
    max-width: 250px;
    margin: auto;
    float: left;
    position: absolute;
    top: 20px;
    right: 200px;
    z-index: 9999;
}

.cookie-consent-banner__header {
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    color: #fff;
}

.cookie-consent-banner__description {
    font-weight: normal;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    padding-right: 0px;
}

.btn-footer {
    background-color: #d37e00;
    padding-right: 40px;
    padding-left: 40px;
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 17px;
    color: white;
    border-radius: 30px;
    font-weight: 500;
}

.cookie-consent-banner__cta--secondary {
    padding: 9px 13px;

    border: 2px solid #3A4649;

    background-color: transparent;

    color: #2CE080;
}

.cookie-consent-banner__cta:hover {
    background-color: #20BA68;
}

.cookie-consent-banner__cta--secondary:hover {
    border-color: #838F93;

    background-color: transparent;

    color: #22C870;
}

.cookie-consent-banner__cta:last-child {
    margin-left: 16px;
}
.cookie-bar {
    position: fixed;
    bottom: 0;
    padding: 12px 15px;
    width: 100%;
    z-index: 15;
    background-size: cover;
    text-align: left;
    z-index: 99999;
    background: #050b33;
    display: none;
}
.cookie-bar p {
    display: inline-block;
    margin-bottom: 0;
    color: #fff;
    font-size: calc(11px + (13 - 11) * (100vw - 320px) / (1920 - 320));
    line-height: 25px;
    margin-right: 10px;
}
.cookie-bar .setcokies {
    color: #fff;
    display: inline-block;
}
.cookie-bar .cookie-btn {
    color: #fff;
    background: #e00030;
    padding: 3px 10px 3px 6px;
    border-radius: 0;
    font-weight: 700;
    font-size: calc(11px + (13 - 11) * (100vw - 320px) / (1920 - 320));
    text-decoration: none;
    text-transform: uppercase;
    margin-left: 0;
}
.cookie-bar .cookie-btn-3 {
    border: 1px solid #fff;
    padding: 2px 4px;
    background: #36454f;
    border-radius: 2px;
    color: #fff;
}

.secondlogo img {
    width: 220px;
  }

  bannersec {
    padding: 0rem;
  }


  .tab-content .main-heading {
    color: inherit;
  }